import React from 'react';
import openSocket from 'socket.io-client';
import { connect } from 'react-redux';
import { receiveChatItem as receiveChatItemAC,
} from '../actions';

const socket = openSocket();

class Chat extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      msg: '',
      msgArray: [],
      _id: '',
      lastMsg: undefined,
    };
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }


  componentWillReceiveProps() {
    const { _id } = this.props.account;
    const connection = {
      emitterId: _id,
      targetId: this.props.match.params.id,
    };
    if (_id && _id !== this.state._id) {
      this.setState({ _id });
      console.log('Emitting', connection);
      socket.emit('chatMessage', connection);
    }
    socket.on(_id, data => {
      /*       if (this.state.timestamp !== data.timestamp) {
        this.setState({ lastMsg: { ...data } }); */
      console.log('receiveChatItem...  ', data);
      this.props.receiveChatItem(data);
      // }
    });
  }


  componentWillUnmount() {
    socket.close();
  }

  handleChange(event) {
    this.setState({ msg: event.target.value });
  }

  handleSubmit(event) {
    const { _id } = this.props.account;
    const newMsg = {
      emitterId: _id,
      targetId: this.props.match.params.id,
      content: this.state.msg,
      timestamp: Date.now(),
    };
    socket.emit(_id, newMsg);
    this.props.addChatHistoryItem(newMsg);
    event.preventDefault();
    this.setState({ msg: '' });
  }


  render() {
    const targetUser = this.props.userList.find(el => el._id === this.props.match.params.id);
    return (
      <div>
        Chat with : { targetUser ? targetUser.name : '' }

        <form onSubmit={this.handleSubmit}>
          <input
            name="msg"
            value={this.state.msg}
            onChange={this.handleChange}
          />
          <button type="submit">Send</button>
        </form>
            Msg :     {this.state.msg} <br /><br />
            result :  {this.state.msgArray}
      </div>
    );
  }
}

const mapStateToProps = state => state.dataReducer;

const mapDispatchToProps = dispatch => ({
  receiveChatItem: item => dispatch(receiveChatItemAC(item)),
});

export default connect(mapStateToProps, mapDispatchToProps)(Chat);



